* {
  padding: 0;
  margin: 0;
}
html,
body {
  height: 100%;
}
body {
  min-width: 320px;
  font-family: Ubuntu, sans-serif;
  font-size: 16px;
  font-weight: normal;
  color: #828282;
  background: #fff;
}
a:visited,
a:link,
a:hover {
  font-family: Montserrat, sans-serif;
  font-size: 17px;
  font-weight: normal;
  text-decoration: none;
  color: #fff;
}
h2 {
  margin-top: 0;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: normal;
  text-transform: uppercase;
}
img {
  max-width: 100%;
  height: auto;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
p.large {
  margin-bottom: 25px;
  font-size: 22px;
  line-height: 30px;
}
.js-modal.active {
  display: block;
}
/* link */
