/* btn
=============================*/
.btn {
  margin: 0 10px;
  padding: 10px 20px;
  font-family: Montserrat, sans-serif;
  font-size: 17px;
  text-transform: uppercase;
  color: #fff;
  border: none;
  border-radius: 3px;
  transition: background .5s;
}
.btn-blue {
  background: #7ea4e8;
}
.btn-blue:hover {
  color: #fff;
  background: #5385e0;
}
.btn-gray {
  color: #fff;
  background: #6a7079;
}
.btn-gray:hover {
  color: #fff;
  background: #52575e;
}
.btn-red {
  background: #900F0F;
}
/* section table
==============================*/
.section-table {
  display: table;
  width: 100%;
  height: 100%;
}
.section-row {
  display: table-row;
}
.section-cell {
  display: table-cell;
  vertical-align: middle;
}
/* tabs
============================*/
.tabs {
  border: 3px solid #ececec;
}
.tabs__list li {
  text-align: center;
}
.tabs__list li.active a {
  color: #9097a1;
}
.tabs__list li:not(.active) {
  background: #ececec;
}
.tabs__list a {
  display: block;
  padding: 22px 0;
  font-family: Ubuntu, sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #abb1ba;
}
.tabs__content a {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  line-height: 25px;
  font-weight: normal;
  color: #9097a1;
}
.tabs__content-item {
  display: none;
  padding: 30px 25px;
}
.tabs__content-item.active {
  display: block;
}
.tabs__content-item h3 {
  text-transform: uppercase;
  text-align: center;
}
.tabs__content-item p {
  margin-bottom: 30px;
}
.tabs__content-text {
  margin-bottom: 30px;
}
.tabs__content-list li {
  margin-left: 30px;
}
.tabs__content-list a {
  font-size: 14px;
}
.tabs__content-list i {
  margin-right: 10px;
}
.tabs__content-list.col-xs-6 {
  padding-right: 0;
}
.tabs__content-img {
  text-align: center;
}
/*collapse
==============================*/
.collapse__title {
  margin-bottom: 25px;
  cursor: pointer;
}
.collapse__title time {
  margin-right: 25px;
  padding: 5px 10px;
  text-transform: uppercase;
  color: #fff;
  background: #6bd8d3;
  border-radius: 2px;
}
.collapse__title h5 {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
}
.collapse__title i {
  float: right;
  color: #6bd8d3;
}
.collapse__content {
  padding: 10px;
}
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .tabs__content-list {
    margin-bottom: 40px;
  }
  .row-item,
  .row-item-col {
    margin-bottom: 50px;
  }
}
/* Extra Small Devices, Phones */
/* pages
========================================= */
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
/* Extra Small Devices, Phones */
/* header
==========================================*/
.page-header {
  width: 100%;
  height: 100%;
  min-height: 500px;
  background: url("../img/2.jpg") no-repeat center;
  background-size: cover;
}
.page-header__content {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 550px;
  text-align: center;
  color: #fff;
}
.page-header__logo {
  margin-bottom: 10px;
}
.page-header__title {
  margin-bottom: 30px;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  text-transform: uppercase;
}
.page-header__description {
  margin-bottom: 40px;
  font-size: 20px;
  line-height: 28px;
}
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
@media only screen and (max-width: 600px) {
  .page-header__content {
    max-width: 350px;
  }
  .page-header__logo {
    max-width: 350px;
    margin-bottom: 10px;
  }
  .page-header__title {
    margin-bottom: 24px;
    font-size: 22px;
  }
  .page-header__description {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;
  }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .page-header__content .btn {
    font-size: 14px;
    margin: 0 5px;
  }
}
/* speakers
==============================================*/
.speakers__header {
  padding-top: 110px;
}
.speakers__header .block {
  max-width: 470px;
}
.speakers__header .block h2 {
  margin-bottom: 30px;
}
/* speakers-body
===============================*/
.speakers-title {
  margin-bottom: 60px;
  text-align: center;
}
.speakers-title h2 {
  margin-bottom: 5px;
}
.speakers-gallery {
  margin-bottom: 70px;
}
.speakers-gallery__item {
  text-align: center;
}
.speakers-gallery__header {
  position: relative;
  margin-bottom: 20px;
  cursor: pointer;
  transition: all .5s;
}
.speakers-gallery__img {
  margin-bottom: 25px;
  overflow: hidden;
}
.speakers-gallery h3 {
  text-transform: uppercase;
}
.speakers-gallery__header:hover .back {
  opacity: 1;
}
.speakers-gallery__header:hover .front {
  opacity: .4;
}
.front {
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -70px;
  display: inline-block;
  vertical-align: middle;
  width: 140px;
  height: 140px;
  border-radius: 50%;
}
.back {
  display: inline-block;
  vertical-align: middle;
  width: 140px;
  height: 140px;
  text-align: center;
  background: #6bd8d3;
  border-radius: 50%;
  opacity: 0;
}
.fa-twitter {
  font-size: 30px;
  line-height: 140px;
  color: #fff;
}
/* speakers-footer
=============================================*/
.speakers-footer {
  padding-bottom: 90px;
  text-align: center;
  border-bottom: 4px solid #f0f0f1;
}
.speakers-footer p {
  margin-bottom: 30px;
}
/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .top-content .block {
    margin-bottom: 50px;
  }
  .speakers-gallery__item {
    margin-bottom: 50px;
  }
  .speakers__header .block p:last-child {
    margin-bottom: 20px;
  }
}
/* Small Devices, Tablets */
/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .speakers__header {
    padding-top: 60px;
  }
}
/* events
==========================*/
.events {
  padding: 90px 0;
}
.events p {
  margin-bottom: 20px;
}
.events .collapse__title .fa {
  position: relative;
  top: 5px;
}
.events .fa-arrow-circle-down {
  margin-right: 8px;
}
.events .fa-arrow-circle-right {
  margin-left: 8px;
}
.events-gallery {
  max-width: 600px;
  height: auto;
  border-radius: 5px;
  overflow: hidden;
  z-index: 1;
}
.events-gallery .slick-dots {
  left: 18px;
  bottom: 25px;
  width: auto;
}
.events-gallery .slick-dots .slick-active button {
  background: #6bd8d3;
}
.events-gallery .slick-dots li {
  margin: 0;
  margin-right: 7px;
}
.events-gallery .slick-dots li button {
  width: 20px;
  height: 20px;
  border: 4px solid #6bd8d3;
  border-radius: 50%;
}
.events-gallery .slick-dots li button:before {
  content: '';
}
@media only screen and (max-width: 480px) {
  .events .collapse__title h5 {
    display: inline-block;
    vertical-align: middle;
    font-size: 15px;
  }
  .events .collapse__title time {
    display: none;
  }
}
@media only screen and (max-width: 480px) {
  .events-gallery .slick-dots {
    bottom: 10px;
  }
  .events-gallery .slick-dots li {
    margin-right: 3px;
  }
  .events-gallery .slick-dots li button {
    width: 10px;
    height: 10px;
    border-width: 2px;
  }
}
/* modal
=======================================*/
.modal {
  position: fixed;
  top: -200px;
  left: 50%;
  display: none;
  padding: 30px;
  text-align: center;
  max-width: 400px;
  background: #cccccc;
  border: 5px solid #900F0F;
  border-radius: 10px;
  z-index: 10;
  opacity: 0;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.8);
}
.modal__title {
  margin-bottom: 20px;
  font-size: 26px;
}
.modal__descripton {
  margin-bottom: 30px;
}
.modal .btn {
  border-radius: 5px;
}
.modal .btn:hover {
  background: #005500;
}
.modal img {
  margin-bottom: 20px;
  text-align: center;
  box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.4);
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: none;
  background: #000;
  opacity: .8;
}
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .modal {
    padding: 10 5px;
    max-width: 200px;
  }
  .modal__description {
    margin-bottom: 15px;
  }
}
